import { useEffect, useState } from "react";
import classes from "./Movies.module.css";
import Movie from "./Movie";
import { Link, useParams } from "react-router-dom";
import { baseUrl, apiKey } from "../../App";
import LoadingSpinner from "./../UI/LoadingSpinner";

const Movies = ({
  title,
  url,
  fetchedMovies,
  limit,
  mediaProp,
  mediaTitleProp,
  isHome,
}) => {
  const [movies, setMovies] = useState([]);
  const [page, setPage] = useState(1);
  const params = useParams();
  const mediaTitle = mediaTitleProp || params.mediaTitle;
  const media = mediaProp || params.media;
  const mediaUrl = baseUrl.concat(
    media ? `/${media}` : "",
    mediaTitle ? `/${mediaTitle}` : "",
    `?api_key=${apiKey}&page=`
  );
  const handleCapitalize = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
  };
  const handleLoadMore = () => {
    setPage(page + 1);
  };

  useEffect(() => {
    const handleFetchMovies = async (url, page) => {
      let promises = [];
      for (let i = 1; i <= page; i++) {
        const req = await fetch(url + i);
        const res = await req.json();
        promises.push(res);
      }
      const allMovies = await Promise.all(promises);
      const mappedAllMovies = allMovies.map((movies) => movies.results);
      const flattedAllMovies = mappedAllMovies.flat();
      setMovies(flattedAllMovies);
    };

    if (url) {
      handleFetchMovies(url, page);
    } else {
      !mediaUrl ? setMovies(fetchedMovies) : handleFetchMovies(mediaUrl, page);
    }
  }, [url, fetchedMovies, mediaTitle, mediaUrl, page]);

  return !movies ? (
    <LoadingSpinner />
  ) : (
    <div className={classes.container}>
      {movies.length === 0 && (
        <h1 className={classes["no-results"]}>No results found.</h1>
      )}
      <div className={classes.title}>
        <h2>
          {title
            ? title.concat(" ", `${handleCapitalize(media)}`)
            : mediaTitle
            ? handleCapitalize(mediaTitle).concat(" ", handleCapitalize(media))
            : " "}
        </h2>
        {isHome && (
          <Link to={`/${media}/${mediaTitle}`}>
            <button className={classes.button}>View more</button>
          </Link>
        )}
      </div>
      <div className={classes["movies-container"]}>
        {movies.slice(0, limit).map((movie) => (
          <Movie
            key={movie.id}
            id={movie.id}
            title={movie.title}
            posterUrl={movie.poster_path}
            media={movie.media_type ? movie.media_type : media}
          />
        ))}
      </div>
      {!isHome && movies.length > 0 && (
        <button className={classes.button} onClick={handleLoadMore}>
          Load more...
        </button>
      )}
    </div>
  );
};

export default Movies;
